import s from './ModalProjectCard.module.scss';
import { BiLinkExternal } from 'react-icons/all';
import { FaGithub,FaEye } from 'react-icons/fa';
import Modal from '../../../components/Modal/Modal';
import Button from '../../../components/UIElements/Button/Button';
import { useParams } from 'react-router-dom';
import { useEffect } from 'react';
import { useModal } from '../../../hooks/modalHook';
import { PROJECTS } from '../../../constants/projects';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import ReactPlayer from 'react-player';
const ModalProjectCard = () => {
  const { id } = useParams();
  const { image, title, links, technologies } = PROJECTS.find(
    (p) => id === p.id,
  );
  const { isVisible, toggleModal } = useModal();

  useEffect(() => {
    toggleModal();
  }, []);
  {
    /* <LazyLoadImage
          alt="project-img"
          src={image.src}
          effect="blur"
          width="100%"
          wrapperClassName={s.image}
          placeholderSrc={image.placeholderSrc}
        /> */
  }
  return (
    <Modal show={isVisible} onClose={toggleModal}>
      <div className={s.cardWrapper}>
        <div className={s.video}>
          <ReactPlayer
            controls
            width="100%"
            height="100%"
            stopOnUnmount={true}
            url={links?.youtube}
          />
        </div>

        <div className={s.cardBody}>
          <h3 className={s.title}>{title}</h3>

          <div className={s.technologies}>
            {technologies.map((tech) => (
              <span>{tech}</span>
            ))}
          </div>
        </div>

        <div className={s.cardFooter}>
          <Button
            style={{ width: '12rem' }}
            className="primary"
            href={links.repo}
            target="_blank"
          >
           <FaGithub />  &nbsp; GitHub
          </Button>

         { links.site != '#' && <Button
            style={{ width: '12rem' }}
            className="primary"
            href={links.site}
            target="_blank"
          >
            
            <FaEye /> &nbsp; Live
          </Button>}
        </div>
      </div>
    </Modal>
  );
};

export default ModalProjectCard;
